export default "<template>\n  <div class=\"MonHocToan\">\n    <raw-loader :mytxt=\"require('!raw-loader!./TestMenu.vue').default\"></raw-loader>\n    <h1>This is TestMenu page</h1>\n    <div>$route.fullPath : {{$route.fullPath}}</div>\n\n    <div>\n      <ul>\n        <li v-for=\"x in ds\" :key=\"x.id\">\n          <template v-if=\"x.children == undefined\">\n            <router-link :to=\"x.path\">{{ x.label }}</router-link>\n          </template>\n          <template v-else>\n            <router-link :to=\"x.path\">{{ x.label }}</router-link>\n            <ul>\n              <li v-for=\"y in x.children\" :key=\"y.id\">\n                <router-link :to=\"y.path\">{{ y.label }}</router-link>\n              </li>\n            </ul>\n          </template>\n        </li>\n      </ul>\n\n    </div>\n  </div>\n</template>\n<script>\nimport menu from '@/menu';\n//\n\nexport default {\n  name: 'Tpl',\n  components: {\n  },\n  data() {\n    return {\n      ds: []\n    }\n  },\n  mounted() {\n    //import menu from '@/menu';\n    let routes = JSON.parse(JSON.stringify(this.$store.state.routes));\n    menu.routes=routes;\n    this.ds=menu.getMenu();\n    //menu.getChild(this.$route.fullPath);\n    console.log(this.$route.fullPath);\n\n    //alert(JSON.stringify(menu.getChild(this.$route.fullPath)))\n  },\n  methods: {\n  }\n}\n</script>\n";